import { db, collection, doc, onSnapshot, setDoc, serverTimestamp } from '../firebase';

const chatsCollection = collection(db, 'chats');

const createChatID = (currentUserUID, otherUserUID) => {
  return currentUserUID < otherUserUID
    ? `${currentUserUID}_${otherUserUID}`
    : `${otherUserUID}_${currentUserUID}`;
};

const sendMessage = async (currentUserUID, otherUserUID, messageText) => {
  try {
    const chatID = createChatID(currentUserUID, otherUserUID);
    const chatDocRef = doc(chatsCollection, chatID);

    const message = {
      text: messageText,
      sender: currentUserUID,
      timestamp: serverTimestamp(), // Include the timestamp here
    };

    await setDoc(
      chatDocRef,
      {
        messages: {
          [Date.now()]: message,
        },
      },
      { merge: true }
    );

    return true;
  } catch (error) {
    console.error('Error sending message:', error);
    return false;
  }
};

const getMessages = (currentUserUID, otherUserUID, callback) => {
  try {
    const chatID = createChatID(currentUserUID, otherUserUID);
    const chatDocRef = doc(chatsCollection, chatID);

    onSnapshot(chatDocRef, (snapshot) => {
      try {
        const messages = snapshot.exists()
          ? Object.values(snapshot.data()?.messages || {})
          : [];

        const allMessages = messages.sort((a, b) => b.timestamp - a.timestamp);
        // Reverse the order of messages to display from bottom to top
        const reversedMessages = allMessages.reverse();

        callback(reversedMessages);
      } catch (error) {
        console.error('Error getting messages:', error);
        callback([]); // Handle errors by returning an empty array or taking appropriate action
      }
    });
  } catch (error) {
    console.error('Error getting messages:', error);
    callback([]); // Handle errors by returning an empty array or taking appropriate action
  }
};

export { sendMessage, getMessages };