import React, { useState, useEffect } from 'react';
import { db, auth } from '../firebase'; // Import your Firebase instance
import { collection, doc, getDoc, getDocs, where, query } from 'firebase/firestore';
import ChatServiceconversation from './ChatServiceconversation';

const ChatHeader = ({ toggleColumns, otherUserUID, currentUserUID }) => {
  const [isMobile, setIsMobile] = useState(false);
  const [photoURL, setPhotoURL] = useState(null);
  const [displayName, setDisplayName] = useState('');
  const [occupation, setOccupation] = useState('');
  const [isShareChatOpen, setIsShareChatOpen] = useState(false);
  const [selectedSession, setSelectedSession] = useState(null);
  const [selectedMessageUID, setSelectedMessageUID] = useState(null);

  useEffect(() => {
    const mediaQuery = window.matchMedia('(max-width: 768px)');
    setIsMobile(mediaQuery.matches);

    const handleResize = () => {
      setIsMobile(mediaQuery.matches);
    };

    mediaQuery.addListener(handleResize);

    return () => {
      mediaQuery.removeListener(handleResize);
    };
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (otherUserUID) {
          const firestore = db;

          const expertAppsRef = collection(firestore, 'expertApplications');
          const expertAppDoc = doc(expertAppsRef, otherUserUID);

          const expertDocSnap = await getDoc(expertAppDoc);

          if (expertDocSnap.exists()) {
            const expertData = expertDocSnap.data();
            if (expertData) {
              setPhotoURL(expertData.photoURL);
              setDisplayName(expertData.displayName ? expertData.displayName : '');
              setOccupation(expertData.occupation ? expertData.occupation : '');
            }
          } else {
            const userProfilesRef = collection(firestore, 'userProfiles');
            const userProfileDoc = doc(userProfilesRef, otherUserUID);

            const docSnap = await getDoc(userProfileDoc);

            if (docSnap.exists()) {
              const userData = docSnap.data();
              if (userData) {
                setPhotoURL(userData.photoURL);
                setDisplayName(userData.displayName ? userData.displayName : '');
                setOccupation(userData.occupation);
              }
            }
          }
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
      }
    };

    fetchData();
  }, [otherUserUID]);

  useEffect(() => {
    console.log('Current User UID:', currentUserUID);
    console.log('Other User UID:', otherUserUID);

    const fetchSessionAndUpdate = async () => {
      try {
        if (otherUserUID && currentUserUID) {
          const firestore = db;
          const conversationsCollectionRef = collection(firestore, 'sharedConversations');
          let discussionID;

          const expertAppsRef = collection(firestore, 'expertApplications');
          const expertAppDoc = doc(expertAppsRef, currentUserUID);
          const expertDocSnap = await getDoc(expertAppDoc);

          if (expertDocSnap.exists()) {
            // Current user is an expert, use otherUserUID first
            discussionID = `${otherUserUID}_${currentUserUID}`;
          } else {
            // Current user is not an expert, use currentUserUID first
            discussionID = `${currentUserUID}_${otherUserUID}`;
          }

          const conversationRef = doc(conversationsCollectionRef, discussionID);
          const conversationSnapshot = await getDoc(conversationRef);

          if (conversationSnapshot.exists()) {
            const selectedSession = conversationSnapshot.data().selectedSession;
            console.log('Found Conversation Document. Selected Session:', selectedSession);
            setSelectedSession(selectedSession);
          } else {
            console.log('Conversation document not found for the specified UIDs.');
          }
        } else {
          console.log('Invalid user UIDs.');
          setSelectedMessageUID(null);
          setSelectedSession(null);
        }
      } catch (error) {
        console.error('Error fetching and updating session:', error);
      }
    };

    fetchSessionAndUpdate();
  }, [otherUserUID, currentUserUID]);
  
  
  const closeShareChat = () => {
    setIsShareChatOpen(false);
  };

    const renderCustomSVG = () => {
      
    
        return (
          <svg
            className="custom-svg-chatheader"
            xmlns="http://www.w3.org/2000/svg"
            color="#212529"
            fill={'none'}
            viewBox="0 0 24 24"
            strokeWidth={1}
            stroke="currentColor"
            height="25"
            width="25"
            onClick={() => {
              setIsShareChatOpen(true);
            }}
          >
    
     <path
      strokeLinecap="round"
      strokeLinejoin="round"
      d="M8.25 7.5V6.108c0-1.135.845-2.098 1.976-2.192.373-.03.748-.057 1.123-.08M15.75 18H18a2.25 2.25 0 002.25-2.25V6.108c0-1.135-.845-2.098-1.976-2.192a48.424 48.424 0 00-1.123-.08M15.75 18.75v-1.875a3.375 3.375 0 00-3.375-3.375h-1.5a1.125 1.125 0 01-1.125-1.125v-1.5A3.375 3.375 0 006.375 7.5H5.25m11.9-3.664A2.251 2.251 0 0015 2.25h-1.5a2.251 2.251 0 00-2.15 1.586m5.8 0c.065.21.1.433.1.664v.75h-6V4.5c0-.231.035-.454.1-.664M6.75 7.5H4.875c-.621 0-1.125.504-1.125 1.125v12c0 .621.504 1.125 1.125 1.125h9.75c.621 0 1.125-.504 1.125-1.125V16.5a9 9 0 00-9-9z"
    />

        </svg>
      );
    
  };


  return (
    <div className="chat-header p-1" style={{ display: 'flex', alignItems: 'center' }}>
      {isMobile && (
        <svg
          onClick={toggleColumns}
          xmlns="http://www.w3.org/2000/svg"
          color="#212529"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth={1}
          stroke="currentColor"
          height="25"
          width="25"
          style={{ marginRight: '8px' }} // Adjust margin as needed
        >
          <path strokeLinecap="round" strokeLinejoin="round" d="M10.5 19.5L3 12m0 0l7.5-7.5M3 12h18" />
        </svg>
      )}

      <div className="small-avatar">
        <img
          className="small-avatar mx-auto user-avatar mb-2"
          src={photoURL}
          alt="Custom 404 Image"
        />
      </div>
      <div className="d-flex flex-column justify-content-start offset-1 pt-2">
        <div>
          <p className="font-weight-bold mb-0">{displayName}</p>
          <p className="text-sm text-muted">{occupation}</p>
        </div>
      </div>
      {renderCustomSVG()}

    {isShareChatOpen && (
  <ChatServiceconversation  currentUserUID={currentUserUID} selectedSession={selectedSession} otherUserUID={otherUserUID} closeShareChat={closeShareChat} />
)}  

    </div>
  );
};

export default ChatHeader;
