import React, { useState, useEffect, useRef } from 'react';
import ChatHeader from './Chatheader';
import { sendMessage, getMessages } from './ChatService';
import { auth, onAuthStateChanged } from '../firebase'; // Replace 'path/to/firebase' with your Firebase import

const RightColumn = ({ isRightColumnVisible, toggleColumns, otherUserUID }) => {
  const [messages, setMessages] = useState([]);
  const chatBoxRef = useRef(null);
  const messageInputRef = useRef(null);
  const [inputValue, setInputValue] = useState('');
  const [currentUserUID, setCurrentUserUID] = useState(null); 
  const [isChattingWithSelf, setIsChattingWithSelf] = useState(false);
  const [textareaHeight, setTextareaHeight] = useState('52px'); 
  const maxTextareaHeight = 200; 

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        setCurrentUserUID(user.uid);
      } else {
        setCurrentUserUID(null);
      }
    });

    return () => unsubscribe(); // Clean up the listener
  }, []);

  useEffect(() => {
    // Update 'isChattingWithSelf' whenever 'currentUserUID' or 'otherUserUID' changes
    setIsChattingWithSelf(currentUserUID === otherUserUID);
  }, [currentUserUID, otherUserUID]);

  useEffect(() => {
    if (currentUserUID && otherUserUID) {
      getMessages(currentUserUID, otherUserUID, (newMessages) => {
        // Reverse the messages here and then set them in the state
        const reversedMessages = newMessages.slice(0).reverse();
        setMessages(reversedMessages);
      });
    }
  }, [currentUserUID, otherUserUID]);

  const scrollToBottom = () => {
    if (chatBoxRef.current) {
      chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
    }
  };


  const isMobile = window.innerWidth <= 767; // Adjust the width threshold as needed

  const handleMessageSend = async () => {
    if (isChattingWithSelf) {
      console.error("Cannot send messages to yourself.");
      console.log('currentUserUID:', currentUserUID);
      console.log('otherUserUID:', otherUserUID);
      return;
    }
    
    if (inputValue.trim() === '') {
      return;
    }
  
    if (currentUserUID && otherUserUID) {
      const success = await sendMessage(currentUserUID, otherUserUID, inputValue);
      if (success) {
        setInputValue('');
        
        // Set focus only if not on mobile
        if (!isMobile) {
          messageInputRef.current.focus();
        }
      }
    } else {
      console.log('currentUserUID:', currentUserUID);
      console.log('otherUserUID:', otherUserUID);
      console.error('currentUserUID or otherUserUID is undefined');
    }
  };

  useEffect(() => {
    // Scroll to bottom after the messages are updated
    scrollToBottom();
  }, [messages]);


  const handleTextareaResize = () => {
    const textarea = messageInputRef.current;
    if (textarea) {
      // Temporarily set height to auto to get accurate scrollHeight
      textarea.style.height = '52px';
      const newHeight = Math.min(textarea.scrollHeight, maxTextareaHeight);
      textarea.style.height = `${newHeight}px`;
    }
  };

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
    handleTextareaResize(); 
  };

  useEffect(() => {
    handleTextareaResize(); 
  }, []);

  useEffect(() => {
    if (inputValue === '') {
      setTextareaHeight('52px');
    }
  }, [inputValue]);

  const formatTimestamp = (timestamp) => {
    if (timestamp) {
      const date = timestamp.toDate();
      const hours = date.getHours().toString().padStart(2, '0'); // Get hours in 24-hour format
      const minutes = date.getMinutes().toString().padStart(2, '0'); // Get minutes
      return `${hours}:${minutes}`; // Format to HH:MM
    }
    return ''; // Return empty string if timestamp is not available
  };

  return (
    <div className={`chat-right ${isRightColumnVisible ? 'show' : 'hide'}`}>
      <ChatHeader toggleColumns={toggleColumns}  otherUserUID={otherUserUID} currentUserUID={currentUserUID}  />
      <div className="chat-column">
    

      <div className="chat-box" ref={chatBoxRef}>
  {Array.isArray(messages) && messages.length > 0 ? (
messages.map((message, index) => (
  message.timestamp ? (
    <div
      key={index}
      className={message.sender === currentUserUID ? 'chat-bubble user-sent' : 'chat-bubble'}
    >
  
        <div className="message">{message.text}</div>
        <div className={`timestamp small-timestamp ${message.sender === currentUserUID ? 'user-sent-timestamp' : 'other-sent-timestamp'}`}>
          {formatTimestamp(message.timestamp)}
    
      </div>
    </div>
  ) : null
))
  ) : (
    <div>No messages yet.</div>
  )}

      </div>
      <div className="chatui-container">
        <div className="userui-input" >
          <button className="svg-container userui-left-button">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              color="#212529"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1}
              stroke="currentColor"
              height="25"
              width="25"
            >
              <path strokeLinecap="round" strokeLinejoin="round" d="M18.375 12.739l-7.693 7.693a4.5 4.5 0 01-6.364-6.364l10.94-10.94A3 3 0 1119.5 7.372L8.552 18.32m.009-.01l-.01.01m5.699-9.941l-7.81 7.81a1.5 1.5 0 002.112 2.13" />
            </svg>
          </button>
          <textarea
            type="text"
            placeholder="Type a message..."
            ref={messageInputRef}
            style={{ height: textareaHeight }} 
            value={inputValue}
            onChange={handleInputChange}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                handleMessageSend();
              }
            }}
            className="message-input scroll-col"
            autoComplete="off"
            inputMode="text"
          />
          <button
            aria-label="Send Message"
            onClick={handleMessageSend}
            className="svg-container userui-right-button "
          >
            <i className="fas fa-paper-plane"></i>
          </button>
        </div>
      </div>
    </div>
    </div>
  );
};

export default RightColumn;